/*  PARTIALS */
@font-face {
  font-family: "Century Gothic Regular";
  src: url("../fonts/Century Gothic Regular.woff2") format("woff2"), url("../fonts/Century Gothic Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "Century Gothic Bold";
  src: url("../fonts/Century Gothic Bold.woff2") format("woff2"), url("../fonts/Century Gothic Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

/*  LIBS */
/* perfect-scrollbar v0.7.0 */
.ps {
  -ms-touch-action: auto;
  touch-action: auto;
  overflow: hidden !important;
  -ms-overflow-style: none;
}

@supports (-ms-overflow-style: none) {
  .ps {
    overflow: auto !important;
  }
}

@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .ps {
    overflow: auto !important;
  }
}

.ps.ps--active-x > .ps__scrollbar-x-rail, .ps.ps--active-y > .ps__scrollbar-y-rail {
  display: block;
  width: 20px;
  border-radius: 10px;
  background-color: #EDEDED;
}

.ps.ps--in-scrolling.ps--x > .ps__scrollbar-x-rail {
  background-color: #eee;
}

.ps.ps--in-scrolling.ps--x > .ps__scrollbar-x-rail > .ps__scrollbar-x {
  background-color: #999;
  height: 11px;
}

.ps.ps--in-scrolling.ps--y > .ps__scrollbar-y-rail {
  background-color: #eee;
}

.ps.ps--in-scrolling.ps--y > .ps__scrollbar-y-rail > .ps__scrollbar-y {
  background-color: #999;
  width: 11px;
}

.ps > .ps__scrollbar-x-rail {
  display: none;
  position: absolute;
  -webkit-transition: background-color .2s linear, opacity .2s linear;
  -o-transition: background-color .2s linear, opacity .2s linear;
  -moz-transition: background-color .2s linear, opacity .2s linear;
  transition: background-color .2s linear, opacity .2s linear;
  bottom: 0px;
  height: 15px;
}

.ps > .ps__scrollbar-x-rail > .ps__scrollbar-x {
  position: absolute;
  background-color: #aaa;
  -webkit-border-radius: 6px;
  -moz-border-radius: 6px;
  border-radius: 6px;
  -webkit-transition: background-color .2s linear, height .2s linear, width .2s ease-in-out, -webkit-border-radius .2s ease-in-out;
  transition: background-color .2s linear, height .2s linear, width .2s ease-in-out, -webkit-border-radius .2s ease-in-out;
  -o-transition: background-color .2s linear, height .2s linear, width .2s ease-in-out, border-radius .2s ease-in-out;
  -moz-transition: background-color .2s linear, height .2s linear, width .2s ease-in-out, border-radius .2s ease-in-out, -moz-border-radius .2s ease-in-out;
  transition: background-color .2s linear, height .2s linear, width .2s ease-in-out, border-radius .2s ease-in-out;
  transition: background-color .2s linear, height .2s linear, width .2s ease-in-out, border-radius .2s ease-in-out, -webkit-border-radius .2s ease-in-out, -moz-border-radius .2s ease-in-out;
  bottom: 2px;
  height: 6px;
}

.ps > .ps__scrollbar-x-rail:hover > .ps__scrollbar-x, .ps > .ps__scrollbar-x-rail:active > .ps__scrollbar-x {
  height: 11px;
}

.ps > .ps__scrollbar-y-rail {
  display: none;
  position: absolute;
  -webkit-transition: background-color .2s linear, opacity .2s linear;
  -o-transition: background-color .2s linear, opacity .2s linear;
  -moz-transition: background-color .2s linear, opacity .2s linear;
  transition: background-color .2s linear, opacity .2s linear;
  right: 0;
}

.ps > .ps__scrollbar-y-rail > .ps__scrollbar-y {
  position: absolute;
  background-color: #02467B;
  -webkit-transition: background-color .2s linear, height .2s linear, width .2s ease-in-out, -webkit-border-radius .2s ease-in-out;
  transition: background-color .2s linear, height .2s linear, width .2s ease-in-out, -webkit-border-radius .2s ease-in-out;
  -o-transition: background-color .2s linear, height .2s linear, width .2s ease-in-out, border-radius .2s ease-in-out;
  -moz-transition: background-color .2s linear, height .2s linear, width .2s ease-in-out, border-radius .2s ease-in-out, -moz-border-radius .2s ease-in-out;
  transition: background-color .2s linear, height .2s linear, width .2s ease-in-out, border-radius .2s ease-in-out;
  transition: background-color .2s linear, height .2s linear, width .2s ease-in-out, border-radius .2s ease-in-out, -webkit-border-radius .2s ease-in-out, -moz-border-radius .2s ease-in-out;
  right: 0;
  width: 20px;
  border-radius: 10px;
  background-color: #00b5b4;
}

.ps:hover.ps--in-scrolling.ps--x > .ps__scrollbar-x-rail {
  background-color: #eee;
}

.ps:hover.ps--in-scrolling.ps--x > .ps__scrollbar-x-rail > .ps__scrollbar-x {
  background-color: #999;
  height: 11px;
}

.ps:hover > .ps__scrollbar-y-rail:hover > .ps__scrollbar-y {
  background-color: #00a6a5;
}

/*  COMPONENTS */
body {
  min-width: 1900px;
  font-family: "Century Gothic Regular", Arial, sans-serif;
  font-size: 14px;
  line-height: 1.4;
  color: #6a6a6a;
}

.out {
  overflow: hidden;
}

.container {
  max-width: 1770px;
  margin: 0 auto;
  padding: 0 15px;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: normal;
}

.title-lg {
  font-family: "Century Gothic Bold", Arial, sans-serif;
  font-size: 100px;
  line-height: 1.2;
  text-transform: uppercase;
  word-spacing: .2em;
  color: #fff;
}

.title-md {
  font-family: "Century Gothic Bold", Arial, sans-serif;
  font-size: 68px;
  line-height: 1.2;
  color: #6a6a6a;
}

.title-sm {
  font-family: "Century Gothic Bold", Arial, sans-serif;
  font-size: 50px;
  text-transform: uppercase;
  color: #fff;
  letter-spacing: .06em;
  word-spacing: .2em;
}

/*  PAGES */
.section {
  height: 1080px;
  background-color: #fbfbfb;
}

.section__top {
  position: relative;
  min-height: 250px;
  height: 0;
  background: url(../img/bg/section_bg.png) 50% -4%/cover no-repeat;
}

.section__top::before, .section__top::after {
  content: '';
  position: absolute;
  bottom: 0;
  z-index: 1;
  background: #fbfbfb;
}

.section__top::before {
  right: 31%;
  transform: skewX(-56deg);
  height: 50px;
  width: 130px;
}

.section__top::after {
  right: 0;
  height: 50px;
  width: 670px;
}

.section__top_color-1 {
  background-color: #da2032;
}

.section__top_color-2 {
  background-color: #00b5b4;
}

.section__container {
  height: 100%;
}

.section__container::before {
  content: '';
  display: inline-block;
  vertical-align: middle;
  height: 100%;
}

.section__title {
  display: inline-block;
  vertical-align: middle;
  margin-top: -15px;
}

.section_has-decor {
  position: relative;
}

.section_has-decor::after {
  position: absolute;
  left: -10px;
  right: -10px;
  bottom: 0;
  z-index: 1;
  height: 160px;
  background: url(../img/bg/section_decor-bot.svg) no-repeat top center/cover;
  content: '';
}

.event {
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: calc(100vh - 250px);
  min-height: 730px;
}

.event__in {
  margin-top: -80px;
}

.event__descr {
  max-width: 1500px;
  margin: 50px auto 0;
  text-align: center;
  font-size: 60px;
  line-height: 1.33333;
}

.event__title {
  font-size: 0;
}

.event__date {
  display: inline-block;
  vertical-align: bottom;
  width: 295px;
  margin-right: 95px;
  font-size: 55px;
  line-height: 1.33333;
}

.event__text {
  display: inline-block;
  vertical-align: bottom;
  width: calc(100% - 390px);
}

.table-wrapper {
  max-width: 1760px;
  margin: 0 auto;
  padding: 60px 0 0;
}

.event-table {
  font-size: 14px;
  overflow: hidden;
  line-height: 1.2;
}

.event-table__in {
  position: relative;
  padding-right: 40px;
  height: 640px;
}

.event-table__head {
  max-width: 1720px;
  display: table;
  table-layout: fixed;
  width: 100%;
  font-size: 13px;
  color: #000;
  border-radius: 4px 4px 0 0;
  background-color: #d9ff38;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.04);
}

.event-table__head .event-table__cell {
  border-bottom: none;
}

.event-table__body {
  display: table;
  table-layout: fixed;
  width: 100%;
  max-width: 1720px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.04);
  border-radius: 0 0 4px 4px;
  background: #fff;
  overflow: hidden;
}

.event-table__body .event-table__cell {
  padding: 25px 0;
}

.event-table__row {
  display: table-row;
}

.event-table__cell {
  display: table-cell;
  padding: 12px 0;
  vertical-align: middle;
  text-align: center;
  border-bottom: 1px solid #F1F1F1;
}

.event-table__cell_font {
  font-size: 16px;
}

.event-table__cell:nth-child(1) {
  width: 22%;
}

.event-table__cell:nth-child(2) {
  width: 20%;
}

.event-table__cell:nth-child(3) {
  width: 24%;
}

.event-table__cell:nth-child(4) {
  width: 39%;
}

.event-table__cell:nth-child(5) {
  width: 28%;
}

.event-table__cell:nth-child(6) {
  width: 32%;
}

.event-table__cell:nth-child(7) {
  width: 20%;
}

.event-table__cell:nth-child(8) {
  width: 22%;
}

.event-table__cell:nth-child(9) {
  width: 37%;
}

.event-table__cell:nth-child(10) {
  width: 26%;
}

.event-table__cell:nth-child(11) {
  width: 15%;
}

.event-table__cell:nth-child(12) {
  width: 28%;
}

.event-table a {
  display: block;
  color: #6a6a6a;
  text-decoration: none;
}

.event-table span {
  display: block;
}
